import React, { Component } from 'react'
import Slider from 'react-slick'
import thumb from './assets/thumb.png'
import logo from './assets/logo.png'
import customCenter from './assets/customCenter.png'
import rightControl from './assets/rightControl.png'
import cmpstructure from './assets/cmpstructure.png'
import redux from './assets/redux.png'
import work from './assets/work.png'
import board from './assets/board.png'
import pcLogo from './assets/pcLogo.png'
import tools from './assets/tools.png'
import social from './assets/social.png'
import slogan1Light from './assets/slogan1Light.png'
import slogan2Light from './assets/slogan2Light.png'
import slogan3Light from './assets/slogan3Light.png'
import learn1 from './assets/learn1.png'
import learn2 from './assets/learn2.png'
import learn3 from './assets/learn3.png'
import fail from './assets/fail.png'
import download from './assets/download.png'
import account from './assets/account.png'
import prd from './assets/prd.png'
import swagger from './assets/swagger.png'
import tdd from './assets/tdd.png'
import dontknow from './assets/dontknow.jpeg'
import speed from './assets/speed.jpeg'
import focus from './assets/focus.jpg'
import './App.css'

const profit = [
  {
    head: '收获',
    title: '积淀了一些工程经验',
    detail: [
      {
        caption: '前端架构',
        img: social
      },
      {
        caption: '最佳工程实践',
        img: board
      },
      {
        caption: '工作站',
        img: pcLogo
      },
      {
        caption: '工具',
        img: tools
      }
    ]
  },
  {
    head: '收获',
    title: '跟小伙伴学到了很多',
    detail: [
      {
        caption: '',
        img: learn1
      },
      {
        caption: '各种黑科技',
        img: learn2
      },
      {
        caption: '',
        img: learn3
      }
    ]
  },
  {
    head: '不足',
    title: 'bugs...',
    detail: [
      {
        caption: 'cmp无法访问',
        img: fail
      },
      {
        caption: '用户端下载版本',
        img: download
      },
      {
        caption: '学校账号配置',
        img: account
      }
    ]
  },
  {
    head: '不足',
    title: '影响效率的点...',
    detail: [
      {
        caption: '前后端接口依赖',
        img: swagger
      },
      {
        caption: '测试驱动',
        img: tdd
      },
      {
        caption: '从业务出发',
        img: prd
      }
    ]
  },
  {
    head: '不足',
    title: '代码之外...',
    detail: [
      {
        caption: '欲速则不达',
        img: speed
      },
      {
        caption: '专注度',
        img: focus
      },
      {
        caption: '知其然不知其所以然',
        img: dontknow
      }
    ]
  },
  {
    head: '下阶段目标',
    title: '',
    detail: [
      {
        caption: '提升效率',
        img: slogan1Light
      },
      {
        caption: '提高性能',
        img: slogan2Light
      },
      {
        caption: '深挖技术',
        img: slogan3Light
      }
    ]
  }
]

class App extends Component {
  render() {
    var slickSettings = {
      autoplay: false,
      accessibility: false,
      dots: true,
      arrows: true,
      swipeToSlide: true,
      infinite: true,
      speed: 100,
      autoplaySpeed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: false,
      pauseOnHover: false
    }
    const profits = profit.map((p, i) => {
      let imgs = p.detail.map((d, ind) => (
        <div key={`detail${ind}`} className="profitDetail">
          <img src={d.img} alt={d.caption} />
          <p>{d.caption}</p>
        </div>
      ))
      return (
        <div className="page">
          <h3>{p.head}</h3>
          <div className="profitPage">
            <p className="title">{p.title}</p>
            <div className="profitItems">{imgs}</div>
          </div>
        </div>
      )
    })
    return (
      <div className="App">
        <header className="App-header">
          <img src={thumb} className="App-logo" alt="logo" />
          <h1>2017小回首</h1>
        </header>
        <section id="sliderZone">
          <Slider {...slickSettings}>
            <div className="page">
              <div className="cover">
                <h1>一个小小的回顾</h1>
                <img src={work} alt="cover" />
              </div>
            </div>
            <div className="page">
              <h3>后半段的主要工作</h3>
              <div className="workPage">
                <p>权限控制</p>
                <img src={rightControl} alt="rightControl" />
              </div>
            </div>
            <div className="page">
              <h3>后半段的主要工作</h3>
              <div className="workPage">
                <p>客服中心改版</p>
                <img src={customCenter} alt="customcenter" />
              </div>
            </div>
            <div className="page">
              <h3>后半段的主要工作</h3>
              <div className="workPage">
                <p>重构1 CMP项目结构, Redux状态库</p>
                <img src={cmpstructure} alt="customcenter" />
              </div>
            </div>
            <div className="page">
              <h3>后半段的主要工作</h3>
              <div className="workPage">
                <p>重构2 规范样式: 拆分，复用</p>
                <img src={cmpstructure} alt="customcenter" />
              </div>
            </div>
            <div className="page">
              <h3>后半段的主要工作</h3>
              <div className="workPage">
                <p>重构3 代码规范</p>
                <img src={cmpstructure} alt="customcenter" />
              </div>
            </div>

            {profits}
          </Slider>
        </section>
        <footer>
          <img src={logo} alt="logo" />
          <h1 className="App-title">感谢各位的一路相伴！</h1>
        </footer>
      </div>
    )
  }
}

export default App



